var aos = "data-aos";
const projects = () => {
    //--------------------------------------------Array de objetos------------------------------------------------ 

    let projects = [
        {
            "name": "Pokedex",
            "description": "Pokedex que consulta PokeAPI para obtener información de los pokemones que el usuario quiera consultar.",
            "technologies": ["HTML5", " CSS3", " JavaScript"],
            "link": "https://pokedexlx.netlify.app",
            "img": "build/img/sitesImg/12.webp",
            "repo": "https://github.com/AlejandroMorales-s/pokedex",
            "id": "12"
        },
        {
            "name": "Fiber",
            "description": "Sitio para crear un portafolio de manera fácil y rápida.",
            "technologies": ["HTML5", " CSS3", " SASS"],
            "link": "https://fiberam.netlify.app",
            "img": "build/img/sitesImg/11.webp",
            "repo": "https://github.com/AlejandroMorales-s/Fiber",
            "id": "11"
        },
        {
            "name": "Vaccination",
            "description": "Página web para dar información sobre vacunación.",
            "technologies": ["HTML5", " CSS3", " SASS", " JavaScript"],
            "link": "https://vaccinationlx.netlify.app",
            "img": "build/img/sitesImg/10.webp",
            "repo": "https://github.com/AlejandroMorales-s/vaccination",
            "id": "10"
        },
        {
            "name": "Pastelería",
            "description": "Este sitio es pensado para que los clientes de una pastelería puedan ordenar en línea.",
            "technologies": ["HTML5", " CSS3"],
            "link": "https://pastelerialx.netlify.app",
            "img": "build/img/sitesImg/9.webp",
            "repo": "https://github.com/AlejandroMorales-s/pasteleria",
            "id": "9"
        },
        {
            "name": "Administración Pastelería",
            "description": "Sitio web para administrar pedidos de una pastelería, sabores disponibles y adornos.",
            "technologies": ["HTML5", " CSS3"],
            "link": "https://pastelerialxadmin.netlify.app",
            "img": "build/img/sitesImg/8.webp",
            "repo": "https://github.com/AlejandroMorales-s/pasteleria-admin",
            "id": "8"
        },
        {
            "name": "Blog De Café",
            "description": "Este sitio web es un blog donde se habla sobre temas relacionados con el café y a su elaboración.",
            "technologies": ["HTML5", " CSS3"],
            "link": "https://blogcafe19.netlify.app",
            "img": "build/img/sitesImg/7.webp",
            "repo": "https://github.com/AlejandroMorales-s/Blog-de-cafe",
            "id": "7"
        },
        {
            "name": "Rock & EDM Festival",
            "description": "Esta página es sobre un festival de música, aquí se muestra información relacionada con este evento.",
            "technologies": ["HTML5", " CSS3", " SASS", " JavaScript"],
            "link": "https://rockandedm.netlify.app",
            "img": "build/img/sitesImg/6.webp",
            "repo": "https://github.com/AlejandroMorales-s/rock-edm",
            "id": "6"
        },
        {
            "name": "Front End Store",
            "description": "Este sitio simula una tienda virtual donde se venden playeras.",
            "technologies": ["HTML5", " CSS3"],
            "link": "https://frontendstore1.netlify.app",
            "img": "build/img/sitesImg/5.webp",
            "repo": "https://github.com/AlejandroMorales-s/Fiber",
            "id": "5"
        },
        {
            "name": "Fotomatic",
            "description": "Esta página está relacionada con la fotografía.",
            "technologies": ["HTML5", " CSS3"],
            "link": "https://fotomatic2.netlify.app",
            "img": "build/img/sitesImg/4.webp",
            "repo": "https://github.com/AlejandroMorales-s/Fotomat",
            "id": "4"
        },
        {
            "name": "Colmar Academy",
            "description": "Esta página web se trata de una academia, donde se muestran los cursos que ofrecen, entre otras cosas.",
            "technologies": ["HTML5", " CSS3"],
            "link": "https://colmaracademy1.netlify.app",
            "img": "build/img/sitesImg/3.webp",
            "repo": "https://github.com/AlejandroMorales-s/colmarcademy",
            "id": "3"
        },
        {
            "name": "Tea Cozy",
            "description": " Esta página es de un negocio donde se vende té.",
            "technologies": ["HTML5", " CSS3"],
            "link": "https://teacozy1.netlify.app",
            "img": "build/img/sitesImg/2.webp",
            "repo": "https://github.com/AlejandroMorales-s/Tea-Cozy",
            "id": "2"
        },
        {
            "name": "Freelancer",
            "description": "Página web pensada para un freelancer que quiera explicar lo que hace.",
            "technologies": ["HTML5", " CSS3"],
            "link": "https://serviciosfreelancer.netlify.app",
            "img": "build/img/sitesImg/1.webp",
            "repo": "https://github.com/AlejandroMorales-s/freelancer",
            "id": "1"
        },
    ] 
    for (i in projects) { // "i" es el índice de cada elemento del array, o sea es cada objeto del array
        
        //--------------------------------------------Datos------------------------------------------------ 

        // Se obtienen los datos del array de objetos
        let project = projects[i].name;
        let description = projects[i].description;
        let technologies = projects[i].technologies;
        let link = projects[i].link;
        let img = projects[i].img;
        let repo = projects[i].repo;

        //--------------------------------------------Imagen------------------------------------------------ 

        // Se crea la img
        let img_project = document.createElement("img");
        img_project.loading = "lazy";
        img_project.src = img;

        // Se crea el div de la img
        let img_div = document.createElement("div");
        img_div.classList.add("project_img");
        img_div.appendChild(img_project);

        //--------------------------------------------Info------------------------------------------------ 

        // Se crea el name del project
        let project_name = document.createElement("h3");
        project_name.classList.add("project_name");
        project_name.innerHTML = project;

        // Se crea la descripción del project
        let project_description = document.createElement("p");
        project_description.innerHTML = description;

        // Se crea el p de las technologies
        let project_technologies = document.createElement("p");
        project_technologies.classList.add("project_technologies");
        project_technologies.textContent = technologies;

        // Se crea div de la info
        let info_div = document.createElement("div");
        info_div.classList.add("project_info");
        info_div.appendChild(project_name);
        info_div.appendChild(project_description);
        info_div.appendChild(project_technologies);

        //--------------------------------------------Btns------------------------------------------------ 

        // Se crea el btn de "Site"
        let project_link = document.createElement("a");
        project_link.classList.add("project_link");
        project_link.href = link;
        project_link.target = "_blank";
        project_link.innerHTML = "Site";

        // Se crea el btn de "Repository"
        let project_repo = document.createElement("a");
        project_repo.classList.add("repo_link");
        project_repo.href = repo;
        project_repo.target = "_blank";
        project_repo.innerHTML = "Repository";

        // Se crea el div de los botones
        let buttons_div = document.createElement("div");
        buttons_div.classList.add("project_buttons");
        buttons_div.appendChild(project_repo);
        buttons_div.appendChild(project_link);

        //--------------------------------------------Enzamble------------------------------------------------ 

        // Se agrega la info, img y botones al div del proyecto
        let project_div = document.createElement("div");
        project_div.classList.add("project_card"); //`project_card ${technologies}`
        project_div.dataset.aos = "fade-up";
        project_div.appendChild(img_div);
        project_div.appendChild(info_div);
        project_div.appendChild(buttons_div);

        // Se agrega el div del proyecto al div de proyectos
        document.getElementById("projects").appendChild(project_div);           
    }
}
projects();  

